import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './matchRate.scss'

import LessIcon from '../img/IMI-icons/IMI-icon-less.svg'
import MoreIcon from '../img/IMI-icons/IMI-icon-more.svg'

export default class MatchRate extends Component {
    textColor(taux) {
        switch (true) {
            case (taux >= 75) :
                return "text-green"
            case (taux >= 50) :
                return "text-orange"
            case (taux < 50) :
                return "text-red"
            default :
                console.log("does not work")
        }
    }
    
    render() {
        const {indicateur, displayAuto, displayHuman, toggleHuman, toggleAuto} = this.props
        return(
            <div className="matchRate">
                <div className="bg-grey d-flex">
                    <span className={this.textColor(indicateur.taux)+" font-weight-bold bg-white increased-font-size"}
                    >
                        {indicateur.taux}%
                    </span>
                    {/* Fleche en blanc */}
                    <span className="arrow-right"></span>
                    <span className="label increased-font-size">Taux de correspondance</span>
                    <div className="toggle-detail">
                        {(!displayAuto && !displayHuman) &&
                            <div onClick={toggleHuman} className="increased-font-size">
                                <img className="icon" src={MoreIcon} />
                                afficher le détail
                            </div>
                        }
                        {(displayAuto || displayHuman) &&
                            <div onClick={displayAuto ? toggleAuto : toggleHuman}>
                                <img className="icon" src={LessIcon} />
                                masquer le détail
                            </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}

MatchRate.propTypes = {
    indicateur: PropTypes.object,
}