import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './thumbnailImage.scss'

export default class ThumbnailImage extends Component {
    sourceColor(category) {
        switch(category) {
            case 'patrimoine':
                return "yellow"
            case 'medias':
                return "blue"
            case 'reseaux':
                return "purple"
            default:
                return
        }
    }


    render() {
        const {image,category, displayColumn} = this.props
        return(
            <div className='thumbnail-image'>
                    <img src={image.path} alt={image.description}/>
                        <div className="bg-dark text-white date-photo image-label increased-font-size">
                            {image.datePhoto}
                        </div>
                        
                        <div className={this.sourceColor(category) + " source image-label increased-font-size"}>
                            {image.source}
                        </div>
                    
                    {!displayColumn && <div className={this.sourceColor(category) + " category image-label increased-font-size"}>{category}</div>}
            </div>
        )
    }
}

ThumbnailImage.propTypes = {
    image: PropTypes.object,
    category: PropTypes.string,
    displayColumn: PropTypes.bool,
}