import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './columns.scss';

import ThumbnailList from './thumbnailList';

export default class Columns extends Component {

    renderColumns(displayColumn, thumbnail){
        if (displayColumn) {
            return (
                <div className='columns row'>
                    <div className='column patrimoine col-4'>
                        <h2 className="yellow increased-font-size">Patrimoine</h2>
                        <ThumbnailList filter='patrimoine' thumbnail={thumbnail} displayColumn={displayColumn}/>
                    </div>
                    <div className='column medias col-4'>
                        <h2 className="blue increased-font-size">Médias</h2>
                        <ThumbnailList filter='medias' thumbnail={thumbnail} displayColumn={displayColumn}/>
                    </div>
                    <div className='column reseaux-sociaux col-4'>
                        <h2 className="purple increased-font-size">Réseaux sociaux</h2>
                        <ThumbnailList filter='reseaux' thumbnail={thumbnail} displayColumn={displayColumn}/>
                    </div>
                </div>
            )
        } else {
            return(
                <div className='columns'>
                    <ThumbnailList filter='' thumbnail={thumbnail} displayColumn={displayColumn}/>
                </div>
            )
        }
        
    }

    render() {
        const {thumbnail, displayColumn} = this.props
        return(
            <>
                {this.renderColumns(displayColumn, thumbnail)}
            </>
        )
    }
}

Columns.propTypes = {
    thumbnail: PropTypes.array,
    displayColumn: PropTypes.bool,
}