import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './searchbar.scss';

import { FaSearch } from 'react-icons/fa';


export default class Searchbar extends Component {

    renderKeyWords(searchWords){
        return(
            searchWords.map(keyword => {
                return(
                    <div key={keyword.id} className="bg-dark text-white m-2 p-2 h-100">
                        {keyword.word}
                    </div>
                )
                
            })
        )
    }

    render() {
        const {searchWords} = this.props
        return(
            <div className="searchbar d-flex align-content-center">
                <div className="bg-dark text-white m-2 pt-2 px-2">
                    <FaSearch />
                </div>
                {this.renderKeyWords(searchWords)}
            </div>
        )
    }
}

Searchbar.propTypes = {
    searchWords: PropTypes.array,
}