import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './thumbnailList.scss'

import Thumbnail from './thumbnail';

export default class ThumbnailList extends Component{

    // original solution, change if not enough performances
    renderThumbnailList(filter, thumbnail, displayColumn) {
        // console.log('filter '+filter)
        // console.log('category '+ thumbnail.category)
        // console.log(filter === thumbnail.category || filter === '')
        const filteredThumbnail = thumbnail.filter( thumb => (filter === thumb.category || filter === ''))
        return (filteredThumbnail.map(thumb => {
            return(
                <div key={thumb.id} className='thumb'>
                    <Thumbnail filter={filter} thumb={thumb} displayColumn={displayColumn}/>
                </div>
            )
        }))
    }

    // Supposedly best solution in performances
    // but arguably less clear and needs implementation
    //
    // renderThumbnails = (filter, thumbnail) => {
    //     return thumbnail.reduce((acc, thumb) => {
    //         if (filter === '' || filter === thumb.category) {
    //             return acc
    //         }
    //         acc.push(( 
    //             <div className = 'thumb'>
    //                 <h1> hello </h1> 
    //                 { /* <Thumbnail thumb={thumb} /> */ }
    //             </div>
    //         ))
    //         return acc
    //     }, [])
    // }
    
    render(){
        const {filter, thumbnail, displayColumn} = this.props
        return(
            <div className="thumbnail-list">
                {this.renderThumbnailList(filter, thumbnail, displayColumn)}
            </div>
        )
    }
}

ThumbnailList.propTypes = {
    filter: PropTypes.string,
    thumbnail: PropTypes.array,
    displayColumn: PropTypes.bool,
}