import React, { Component } from "react";
import PropTypes from "prop-types";

import "./thumbnailDescription.scss";

import MatchRate from "./matchRate";
import Automated from "./automated";

import HumanIcon from '../img/IMI-icons/IMI-icon-human.svg'
import LessIcon from '../img/IMI-icons/IMI-icon-less.svg'
import MoreIcon from '../img/IMI-icons/IMI-icon-more.svg'

export default class ThumbnailDescription extends Component {
  constructor(props){
    super(props)
    this.state={
    displayHuman: false,
    displayAuto: false,
    }
  }
  
  shorten = (text, maxLength) => {
    if (text && text.length > maxLength) {
      return text.substr(0, maxLength) + "...";
    } else {
      return text;
    }
  };

  toggleAuto = (event) => {
    event.preventDefault()
    this.setState({
      displayAuto: !this.state.displayAuto
    })
    console.log('displayAuto :')
    console.log(this.state.displayAuto)
  }

  toggleHuman = (event) => {
    event.preventDefault()
    this.setState({
      displayHuman: !this.state.displayHuman
    })
    console.log('displayHuman :')
    console.log(this.state.displayHuman)
  }

  render() {
    
    const { description, indicateur } = this.props;
    const { displayAuto, displayHuman} = this.state;
    return (
      <div className="description text-left">
        <div>
          <MatchRate
            indicateur={indicateur}
            displayAuto={displayAuto}
            displayHuman={displayHuman}
            toggleHuman={this.toggleHuman}
            toggleAuto={this.toggleAuto}
          />
        </div>
        
        {displayAuto &&(
          <div className="description-header bg-dark text-white increased-font-size">
            <img className="icon" src={HumanIcon} alt="Indexation Humaine" />
            Indexation Humaine
          </div>
        )}
        

        <div className="p-2">
          <div className="font-weight-bold mb-1 increased-font-size">{description.title}</div>

          {!displayHuman && !displayAuto && 
            <div>
              {this.shorten(description.content, 200)}
            </div>
          }
          
          {(displayHuman || displayAuto) && 
            <>
              <div>{description.content}</div>
              <br />
              <div>Publié le {description.datePublication}</div>
              <div>Par {description.auteur}</div>
              <br />
              <div>
                {description.motsCle.map((mot) => {
                  return mot + ", ";
                })}
              </div>
            </>
          }
        </div>

        {displayAuto &&
          <>
            <Automated indicateur={indicateur} />
          </>
        }

        
        
        <div
          onClick={this.toggleAuto}
          className="increased-font-size"
        >
          {!displayAuto &&
            <img className="icon" src={MoreIcon} />
          }
          {displayAuto &&
            <img className="icon" src={LessIcon} />
          }
          détail du document
        </div>

      </div>
    );
  }
}

ThumbnailDescription.propTypes = {
  description: PropTypes.object,
  indicateur: PropTypes.object,
};
