import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './automated.scss'

import InfoIcon from '../img/IMI-icons/IMI-icon-information.svg'
import MachineIcon from '../img/IMI-icons/IMI-icon-machine.svg'

export default class Automated extends Component{
    
    renderLabelList(labels){
        return (labels.map(label => {
            return(
                <div key={label.title} className="row ml-0">
                    <div className="col-8 p-0">
                        {label.title}
                    </div>
                    <div className="col-4 p-0 pr-2 d-flex justify-content-end">
                        {label.rate}%
                    </div>
                </div>
            )
        })) 
    }

    renderText2Speech(text2Speech){
        return (text2Speech.map(item => {
            return(
                <div key={item.title} className="row ml-0">
                    <div className="col-4 p-0">
                        {item.title}
                    </div>
                    <div className="col-2 p-0 d-flex justify-content-end">
                        {item.timestamp}
                    </div>
                    <div className="col-4 pr-2 d-flex justify-content-end">
                        {item.rate}%
                    </div>
                </div>
            )
        })

        )
    }
    
    render() {
        const {indicateur} = this.props
        return(
            <div className="automated">
                <div className="description-header bg-dark text-white increased-font-size">
                    <img className="icon" src={MachineIcon} />
                    Detection Automatique
                </div>
                <div className="automated-content m-2">
                    <div className="labels col-6 mb-2 p-0">
                        <div className="title row ml-0">
                            <div className="col-4 p-0">
                                Labels
                            </div>
                            <div className="col-8 p-0 d-flex justify-content-end">
                                Taux de fiabilité
                                <img className="icon" src={InfoIcon} />
                            </div>
                        </div>
                        {this.renderLabelList(indicateur.labels)}
                    </div>
                    <div className="t2s">
                        <div className="title row ml-0">
                            <div className="col-3 p-0">
                                Text2Speech
                            </div>
                            <div className="col-3 p-0 d-flex justify-content-end">
                                Timestamp
                            </div>
                            <div className="col-4 p-0 d-flex justify-content-end">
                                Taux de fiabilité
                                <img className="icon" src={InfoIcon} />
                            </div>
                        </div>
                        {this.renderText2Speech(indicateur.text2Speech)}
                    </div>
                </div>
            </div>
        )
    }
}

Automated.propTypes = {
    indicateur: PropTypes.object,
}